import React from "react";

const Home = () => (
    <div style={style.container}>
        <img
            style ={style.imageCover}
            src="https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/bg-home.jpg"
            alt="Belajar Coding"
        />
        <div style={style.blockText}>
            <h1>Belajar Coding Sekarang</h1>
            <h4>Lebih dari 2.000 modul belajar pemrograman dalam beragam format yang bisa kamu pelajari.</h4>

        </div>
    </div>
);

const style = {
    container: {
        position: 'relative',
        textAlign: 'center',
    },
    imageCover: {
        width: '100%'
    },
    blockText: {
        width: '100%',
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        color: 'white'
    }
};

export default Home;
